@import url(https://fonts.googleapis.com/css?family=Roboto&display=swap);
.spinner{-webkit-animation:rotate 2s linear infinite;animation:rotate 2s linear infinite;z-index:2;position:absolute;top:50%;left:50%;margin:-25px 0 0 -25px;width:50px;height:50px}.spinner .path{stroke:#4b2968;stroke-linecap:round;-webkit-animation:dash 1.5s ease-in-out infinite;animation:dash 1.5s ease-in-out infinite}@-webkit-keyframes rotate{100%{-webkit-transform:rotate(360deg);transform:rotate(360deg)}}@keyframes rotate{100%{-webkit-transform:rotate(360deg);transform:rotate(360deg)}}@-webkit-keyframes dash{0%{stroke-dasharray:1, 150;stroke-dashoffset:0}50%{stroke-dasharray:90, 150;stroke-dashoffset:-35}100%{stroke-dasharray:90, 150;stroke-dashoffset:-124}}@keyframes dash{0%{stroke-dasharray:1, 150;stroke-dashoffset:0}50%{stroke-dasharray:90, 150;stroke-dashoffset:-35}100%{stroke-dasharray:90, 150;stroke-dashoffset:-124}}

*{box-sizing:border-box;margin:0;padding:0}body{height:100vh;height:calc(1vh * 100);height:calc(var(--vh, 1vh) * 100)}#root{display:flex;justify-content:center;align-items:center;height:100vh;background:#654ea3;background:-webkit-gradient(linear, left top, right top, from(#eaafc8), to(#654ea3));background:linear-gradient(to right, #eaafc8, #654ea3)}#root #quote-box{display:block;font:100% "Roboto",sans-serif;font-size:18px;font-weight:normal;background-color:#ebbed2;width:600px;height:170px;border-radius:10px;box-shadow:10px 10px 20px -11px rgba(128,75,128,0.8)}@media screen and (max-width: 625px){#root #quote-box{justify-content:center;width:310px;height:170px;font-size:16px}}#root #quote-box #text{height:120px;padding:20px 40px 0 40px;color:#4b2968}#root #quote-box #text span{font-size:24px}@media screen and (max-width: 625px){#root #quote-box #text{padding:10px 30px 0 30px}#root #quote-box #text span{font-size:18px}}#root #quote-box #author{position:relative;float:right;width:600px;height:30px;margin-top:15px;margin-bottom:35px;padding-left:325px;color:#4b2968;font-style:italic}@media screen and (max-width: 625px){#root #quote-box #author{width:310px;padding-left:50px}}#root #quote-box #author-anonymous{position:relative;float:right;width:600px;height:30px;margin-top:15px;margin-bottom:35px;padding-left:325px;color:#4b2968;font-style:italic}@media screen and (max-width: 625px){#root #quote-box #author-anonymous{width:310px;padding-left:50px}}#root #new-quote{position:relative;float:left;width:auto;font-weight:500;font-family:"Roboto",sans-serif;font-size:18px;background-color:#ebbed2;color:#4b2968;border-radius:5px;border:none;padding:5px 10px;margin-left:90px;cursor:pointer;box-shadow:10px 10px 20px -11px rgba(128,75,128,0.8)}#root #new-quote:hover{background-color:#4b2968;color:#ebbed2}@media screen and (max-width: 625px){#root #new-quote{display:block;float:none;margin-left:0;margin:0 auto 10px;font-size:16px}}#root #tweet-quote{position:relative;float:right;width:auto;font-weight:500;background-color:#0368db;text-decoration:none;color:#f5f5f5;border-radius:5px;margin-right:90px;padding:5px 10px;cursor:pointer;box-shadow:10px 10px 20px -11px rgba(128,75,128,0.8)}#root #tweet-quote:hover{background-color:#4b2968;color:#ebbed2}@media screen and (max-width: 625px){#root #tweet-quote{display:block;float:none;margin-right:0;margin:0 auto;font-size:16px;width:-webkit-max-content;width:-moz-max-content;width:max-content}}

